.headerOverallContainer {
  display: flex;
  margin: 0;
  height: 98px;
  padding: 0;
  justify-content: center;
  background-color: #f9f9f9;
}
.header {
  display: flex;
  background-color: #f9f9f9;
  flex-direction: row;
  align-items: center;
  width: 1200px;
  top: 20px;
  left: 120px;
  justify-content: space-between;
  gap: 53px;
}
.navAndLogo {
  display: flex;
  align-items: center;
  gap: 24px;
}
.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.logoContent {
  border: 2px solid black;
  display: flex;
  align-items: center;
  width: 113px;
  height: 45px;
  justify-content: center;
}
.logoText {
  font-size: 30px;
  font-family: sans-serif;
  font-weight: 100;
  color: #212121;
}

.verticalLine {
  display: inline-block;
  width: 2px;
  height: 32px;
  background-color: black;
  margin: 0 10px;
}
.subtitle {
  display: flex;
  align-content: end;
  width: 111px;
}
.logoPartners {
  font-family: matter;
  color: #212121;
  width: 111px;
  font-weight: 500;
  font-size: 11px;
  margin-top: 5px;
}

.nav {
  display: flex;
  padding: 20px;
  font-size: 18px;
  gap: 24px;
  height: 26px;
  width: 475px;
  color: #212121;
  justify-content: start;
  margin: 20px 0;
}

.navItem {
  line-height: 20px;
  width: 475px;
  width: 54px;
  height: 20px;
  top: 3px;
  left: 109.66px;
  cursor: pointer;
}

.buttons {
  width: 321px;
  cursor: pointer;
  color: #ffffff;
  display: flex;
  gap: 12px;
}
.customButtonOne {
  background-color: #20888f;
}
.customButtonTwo {
  background-color: #303030;
}
